.viewOfDeck {
  display: grid;
  grid-template-columns: repeat(4, minmax(100px, auto));
  grid-gap: 10px;
  grid-auto-columns: 100px;
  text-align: center;
}
.searchWithDeck {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr auto;
  grid-template:
  "s c c"
  "d d d";
}
.searchDetails {
  grid-area: s;
  grid-column: 1;
  grid-row: 1;
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  color: #48435C;
  border: 1px solid #48435C; 
}
.searchDetails > button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid #48435C; 
  width: 50%;
}
.cardDetails {
  grid-area: c;
  grid-column: 2/4;
  grid-row: 1;
  margin: 5px;
  padding: 10px;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  color: #48435C;
  border-radius: 5px;
  border: 1px solid #48435C; 
}
.deckDetails {
  text-align: center;
  grid-area: d;
  grid-column: 1/4;
  grid-row: 2;
  border: 1px solid black;
  margin: 5px;
  padding: 10px;
  border-radius: 5px;
  color: #48435C;
  border: 1px solid #48435C; 
}
.decks, h3, h1 {
  text-align: center;
  width: 100%;
  margin: 5px;
}
span {
  padding: 5px;
}
.loginNote {
  text-align:center;
  font-size: 30px;
}
.createDeck {
  text-align: center;
}
leftInfo {

}
#rightInfo {

}
form > * {
  margin: 5px;
}


